"use client";

import dynamic from "next/dynamic";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";

// Fix double initialization issue
const LeafletMap = ({ activeDonors }: any) => {
  const center =
    activeDonors.length > 0
      ? [activeDonors[0].location.latitude, activeDonors[0].location.longitude]
      : [21.1466, 79.0889];

  return (
    <MapContainer
      center={center}
      zoom={12}
      style={{ height: "100%", width: "100%" }}
      preferCanvas={true}
    >
      <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />

      {activeDonors.map((d: any, i: number) => (
        <Marker
          key={d._id || i}
          position={[d.location.latitude, d.location.longitude]}
        >
          <Popup>
            <b>{d.fullName}</b>
            <br />
            Blood: {d.bloodType}
          </Popup>
        </Marker>
      ))}
    </MapContainer>
  );
};

export default LeafletMap;
